@import url(https://unicons.iconscout.com/release/v3.0.0/css/line.css);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Inter:wght@300;400;500;600;700&display=swap);

/* .App {
  text-align: center;
} */

.App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  
  .form-label{
    display: block;
      text-align: left;
  }
  .blockContent input{
    display: block !important;
  }
  .nav-item button{
    background: transparent;
    border: none;
    color: var(--bs-dark);
    font-size: 13px;
    background-color: transparent;
    padding: 23px 18px;
    text-transform: uppercase;
    transition: all .3s ease;
    position: relative;
    font-weight: 600;
  }
  .text-left{
    text-align: left;
  }
  .text-left input{
    margin-right: 5px;
  }
  .text-left label{
    margin-right: 15px;
  }
  .blockContent input {
    display: block !important;
    width: 100%;
    padding: 10px;
    font-size: 14px;
    font-weight: 500;
    border: 1px solid var(--bs-border-color);
  }
  .btn-primary {
    background-color: #027984 !important;
    border-color: #027984 !important;
  }
  .text-primary, .filter-search-form i {
    color : #22b783 !important;
  }
  .text-left label {
    font-weight: 500 !important;
  }
  .form-select {
    padding: 0.625rem 2.75rem 0.625rem 2.25rem !important;
  }
  .badge {
    margin-left: 3px !important;
  }
  a.btn {
    margin-left: 3px !important;
  }
  .home-select {
    background: #fff !important;
    height: 62px !important;
    margin-top: -1px !important;
  }
  .card:hover {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2); 
    border-color: #027984;
    transition: border-color 0.3s ease-in-out;
  }

  .secondary-card {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); 
    transition: border-color 0.3s ease-in-out;
  }

  .sortlist-btn-hover:hover {
    background-color: #C1FFC1; /* Change this to the desired hover color */
    color: #2E7D32; /* Change this to the desired text color on hover */
  }

  .reject-btn-hover:hover {
    background-color: #FFCCCC; /* Change this to the desired hover color */
    color: #B71C1C; /* Change this to the desired text color on hover */
  }
  
img.logo-dark {
    height: 80px;
}

.form-label {
    font-weight: 500;
}
.section-title .title {
    color: #027984;
    text-transform: uppercase;
}
.auth-form {
    margin-bottom: 4px;
}

.auth-form span {
    font-size: 14px;
    color: #000000;
}
